  --white-color: #fff;
  --black-color: #000;
  --grey-color: #fffbf5;
  --disabled-color: #d6d6d6;
  --light-blue-color: #008ed6;
  --blue-color: #246ce2;
  --darker-blue-color: #3a486b;
  --dark-blue-color: #1c2a4d;
  --red-color: #d61200;
  --bg-color: #f9f3ec;
  --border-color: #d7d3cd;
}

