.button {
  padding: 5px 15px;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  color: var(--grey-color);
  border-radius: 10px;
  transition: background 0.3s ease, color 0.3s ease;
}
.button__primary {
  background: var(--light-blue-color);
}
.button__primary-active {
  background: var(--dark-blue-color);
}
.button__primary:hover {
  background: var(--blue-color);
}
.button__primary:disabled {
  color: var(--white-color);
  background: var(--disabled-color);
}
.button__secondary {
  transition: border 0.3s ease-in-out, background 0.3s ease-in-out, color 0.3s ease-in-out, opacity 0.3s ease;
  position: relative;
  background: var(--white-color);
  border: 2px solid var(--light-blue-color);
  color: var(--dark-blue-color);
}
.button__secondary-active {
  background: var(--dark-blue-color);
  border: 2px solid var(--dark-blue-color);
  color: var(--white-color);
}
.button__secondary-active::after {
  display: none;
}
.button__secondary:hover::after {
  opacity: 1;
  visibility: visible;
}
.button__secondary::after {
  content: "";
  width: calc(100% + 8px);
  height: calc(100% + 8px);
  border-radius: 10px;
  background: transparent;
  position: absolute;
  top: -4px;
  left: -4px;
  border: 4px solid var(--blue-color);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease;
}
.button__secondary:disabled {
  color: var(--white-color);
  background: var(--disabled-color);
  border-color: transparent;
}
.button__secondary:disabled::after {
  display: none;
}

